@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;600&display=swap');

$font-sizes: (
    '100': 0.686rem,
    '300': 0.875rem,
    '350': 0.938rem, //15px -> default
    '400': 1rem,
    '500': 1.25rem,
    '600': 1.5rem,
    '700': 1.75rem,
    '800': 2rem
);

$font-weights: (
    '200': 200,
    '400': 400,
    '600': 600
);

$font-family: 'Poppins', sans-serif;