@use '../abstracts/typography';
@use '../abstracts/colors';


body {
    font-family: typography.$font-family;
    font-size: map-get(typography.$font-sizes, '350');
    font-weight: map-get(typography.$font-weights, '400');
    background-color: map-get(colors.$colors, 'neutral', 'very-light-gray');
    color: map-get(colors.$colors, 'neutral', 'grayish-blue');
    text-align: center;
    min-height: 100vh;
    line-height: 1.5;
}

h1,
h2 {
    font-weight: map-get(typography.$font-weights, '600');
    font-size: map-get(typography.$font-sizes, '700');
    color: map-get(colors.$colors, 'neutral', 'very-dark-blue');
    line-height: 1.1;
}