@use '../abstracts/colors';
@use '../abstracts/typography';
@use '../abstracts/breakpoints';


.card {
    padding: 2rem;
    border-radius: 0.5rem;
    display: flex;
    flex-direction: column;
    max-width: 25rem;
    background-color: map-get(colors.$colors, 'neutral', 'very-light-gray');
    box-shadow: 0 1rem 2rem map-get(colors.$colors, 'neutral', 'light-gray');

    &__title {
        font-size: map-get(typography.$font-sizes, '500');
        margin-bottom: 0.25rem;
    }

    &__description {
        margin-bottom: 2rem;
    }

    &__img {
        align-self: flex-end;
    }
}

.card:nth-child(1) {
    border-top: .25rem solid map-get(colors.$colors, 'primary', 'cyan');

    @media (min-width: map-get(breakpoints.$breakpoints, 'desktop')) {
        & {
            grid-area: left;
            align-self: center;
        }
    }
}

.card:nth-child(2) {
    border-top: .25rem solid map-get(colors.$colors, 'primary', 'red');

    @media (min-width: map-get(breakpoints.$breakpoints, 'desktop')) {
        & {
            grid-area: top;
            align-self: center;
        }
    }
}

.card:nth-child(3) {
    border-top: .25rem solid map-get(colors.$colors, 'primary', 'orange');

    @media (min-width: map-get(breakpoints.$breakpoints, 'desktop')) {
        & {
            grid-area: bottom;
            align-self: center;
        }
    }
}

.card:nth-child(4) {
    border-top: .25rem solid map-get(colors.$colors, 'primary', 'blue');

    @media (min-width: map-get(breakpoints.$breakpoints, 'desktop')) {
        & {
            grid-area: right;
            align-self: center;
        }
    }
}