@use '../abstracts/breakpoints';


.card-container {
    @media (min-width: map-get(breakpoints.$breakpoints, 'desktop')) {
        & {
            display: grid;
            grid-template-areas:
                'left top right'
                'left bottom right';
            column-gap: 2rem;
            row-gap: 2rem;
        }
    }
}