@use '../abstracts/colors';
@use '../abstracts/typography';


@each $type, $shades in colors.$colors {
    @each $shade, $value in $shades {
        .color-#{$shade} {
            color: $value;
        }
        
    }
}

@each $size, $value in typography.$font-sizes {
    .font-size-#{$size} {
        font-size: $value;
    }
}

@each $weight, $value in typography.$font-weights {
    .font-weight-#{$weight} {
        font-weight: $value;
    }
}

.max-width-32 {
    max-width: 32rem;
}

.horizontal-align {
    margin: auto;
}

.vertical-align {
    display: grid;
    align-content: center;
}

.gap-40 {
    gap: 4rem;
}

.gap-20 {
    gap: 2rem;
}

.text-left {
    text-align: left;
}

.padding-20 {
    padding: 2rem;
}

.margin-bottom-05 {
    margin-bottom: 0.5rem;
}

.margin-bottom-10 {
    margin-bottom: 1rem;
}